.jumbotron{
    text-align:center;
    background-color:yellow;
    margin-top: 10px;
    padding-top: 30px;
    padding-bottom: 5px;
    border-bottom: 5px solid orangered;
    border-top: 5px solid orangered;
    
}
h1 {
    font-family: 'Kalam', cursive;
    font-size: 65px;
    font-weight: bolder;
    color: orangered;
    line-height: 50px;
    text-shadow: 3px 3px 5px black;
}
h3 {
    font-family: 'Cuprum', sans-serif;
    font-size: 35px;
    margin-top: 20px;
}
h5 {
    font-family: 'Cuprum', sans-serif;
    font-size: 25px;
    margin-top: 20px;
    color: orangered;
}